	module.exports = {"data":{"markdownRemark":{"html":"<p>Players to stand in a circle with the order randomized using whatever method you prefer.\nStart by creating a set of linked identifiers following a theme (e.g. numbers, colors, animals, etc.).\nTo do this, point at a person who hasn't been pointed at yet and say something in line with the theme which has been created.\nThe theme can either be stated ahead of time or created organically.\nFor example, the first person says \"cat,\" the second person says \"dog,\" the third person says \"fish,\" etc. with the established pattern being pets created by the second choice of \"dog.\"</p>\n<p>Speed is not the objective.\nTaking care of your scene partner should be a priority over speed.\nExecute as quickly as possible without sacrificing communication.\nThat being said, try to push yourselves and see how fast you can go before everything breaks down.</p>\n<h3>Extend: Where the F--- is the Celery</h3>\n<p>Create two or more patterns (suggested: three) until everyone feels comfortable with each linked set.\nNext, start sending the identifiers around the circle at the same time.\nOnce everyone is comfortable with the simultaneous identifiers, start moving to the next person in the chain when you're activated.</p>","frontmatter":{"title":"Patterns","type":"Warmup","foci":["Observation","Energy"],"minimumPeople":3,"duration":5,"durationType":"constant","peoplePerScene":null}}},"pathContext":{"slug":"/patterns/"}}

